import React from "react";
import PropTypes from "prop-types";

import "./Card.css";

const Card = (props) => {

  let info = props.information;
  return (
    <div className="card" style={{ background: `linear-gradient(to left, ${info.fcolor}, ${info.scolor})`, }}>
      <img src={info.url} alt="lang.svg" />
      <h3>{info.lang}</h3>
    </div>
  );
};

export default Card;
